import "./styles.css";
import { useEffect, useState } from "react";
import Button from "./components/Button";
import Input from "./components/Input";
import Inputradio from "./components/Inputradio";
import Select from "./components/Select";
import Sidebar from "./components/Sidebar";

const initialValues = {
  fat: "",
  top: "",
  lac: "",
  ash: "",
  temp: "",
  dens: ""
};

export default function App() {
  const source = [
    { id: "SELECT", fat: "", top: "", lac: "", ash: "" },
    { id: "LECHE DESN", fat: 0.06, top: 3.46, lac: 4.94, ash: 0.83 },
    { id: "SMP", fat: 0.15, top: 9.24, lac: 13.33, ash: 2.51 },
    { id: "SMP-ST", fat: 0.14, top: 8.46, lac: 13.73, ash: 2.19 },
    { id: "MP80", fat: 0.15, top: 9.15, lac: 1.2, ash: 0.87 },
    { id: "MP85", fat: 0.22, top: 13.19, lac: 1.13, ash: 0.99 },
    { id: "MPI90", fat: 0.23, top: 13.53, lac: 0.48, ash: 0.62 },
    { id: "MC85", fat: 0.22, top: 11.82, lac: 0.9, ash: 0.65 },
    { id: "MCI90", fat: 0.23, top: 11.75, lac: 0.36, ash: 0.26 },
    { id: "WPI90", fat: 0, top: 47.78, lac: 1.66, ash: 1.37 }
  ];
  const [values, setValues] = useState(initialValues);
  const [ts, setTs] = useState("");
  const [top_ts, setTop_ts] = useState("");
  const [dens, setDens] = useState("");

  useEffect(() => {
    const { fat, top, lac, ash, temp } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash);
    setTs(ts.toFixed(1));
    if (ts > 0 && ts < 100) {
      const top_ts = (Number(top) * 100) / Number(ts);
      setTop_ts(top_ts.toFixed(1));
    } else {
      setTop_ts("");
    }
    if (temp > 0 && temp < 100) {
      const waterdensity =
        (999.83952 +
          16.945176 * temp -
          7.9870401 * Math.pow(10, -3) * Math.pow(temp, 2) -
          46.170461 * Math.pow(10, -6) * Math.pow(temp, 3) +
          105.56302 * Math.pow(10, -9) * Math.pow(temp, 4) -
          280.54253 * Math.pow(10, -12) * Math.pow(temp, 5)) /
        (1 + 16.89785 * Math.pow(10, -3) * temp);
      setDens(
        (
          100 /
          (Number(fat) / 920 +
            Number(top) / 1400 +
            Number(lac) / 1650 +
            Number(ash) / 1850 +
            (100 - Number(ts)) / waterdensity)
        ).toFixed(0)
      );
    }
  }, [values]);

  const handleInput = (e) => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const selectChange = (e) => {
    if (e.target.value === "0") {
      setValues({ ...values, temp: 10 });
    } else if (e.target.value === "1") {
      setValues({ ...values, temp: 50 });
    }
  };
  const mas = (e) => {
    e.preventDefault();
    const namebutton = e.target.name;
    if (namebutton === "mastemp") {
      setValues({ ...values, temp: Number(values.temp) + 1 });
    }

    if (namebutton === "masts" || namebutton === "masdens") {
      const inc = 1.01;
      const { fat, top, lac, ash } = values;
      const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
      if (ts > 0 && ts < 55) {
        setValues({
          ...values,
          fat: (Number(fat) * inc).toFixed(3),
          top: (Number(top) * inc).toFixed(3),
          lac: (Number(lac) * inc).toFixed(3),
          ash: (Number(ash) * inc).toFixed(3)
        });
      }
    }
  };
  const menos = (e) => {
    e.preventDefault();
    const namebutton = e.target.name;
    if (namebutton === "menostemp") {
      setValues({ ...values, temp: Number(values.temp) - 1 });
    }

    if (namebutton === "menosts" || namebutton === "menosdens") {
      const inc = 0.99;
      const { fat, top, lac, ash } = values;
      const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
      if (ts > -1 && ts < 57) {
        setValues({
          ...values,
          fat: (Number(fat) * inc).toFixed(3),
          top: (Number(top) * inc).toFixed(3),
          lac: (Number(lac) * inc).toFixed(3),
          ash: (Number(ash) * inc).toFixed(3)
        });
      }
    }
  };
  function product(e) {
    const nameproduct = e.target.value;
    const selected = source.filter((pname) => pname.id === nameproduct);
    setValues({
      ...values,
      fat: selected[0].fat,
      top: selected[0].top,
      lac: selected[0].lac,
      ash: selected[0].ash
    });
  }

  return (
    <>
      <div>
        <Sidebar />
      </div>
      <div className="App">
        <h1>Density Calculator</h1>
        <div className="custom-select">
          <Select onChange={product}></Select>
        </div>
      </div>
      <br></br>
      <div className="App">
        <Inputradio onChange={selectChange}></Inputradio>
      </div>
      <br></br>

      <div className="divTable">
        <div className="divTableBody">
          <div className="divTableRow">
            <div className="divTableCell">Fat</div>
            <div className="divTableCell">
              <Input
                name="fat"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={values.fat}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell"></div>
          </div>
          <div className="divTableRow">
            <div className="divTableCell">TOP</div>
            <div className="divTableCell">
              <Input
                name="top"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={values.top}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell"></div>
          </div>
          <div className="divTableRow">
            <div className="divTableCell">Lac</div>
            <div className="divTableCell">
              <Input
                name="lac"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={values.lac}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell"></div>
          </div>
          <div className="divTableRow">
            <div className="divTableCell">Ash</div>
            <div className="divTableCell">
              <Input
                name="ash"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={values.ash}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell"></div>
          </div>
          <div className="divTableRow1"></div>
          <div className="divTableRow3">
            <div className="divTableCell">TOP/TS</div>
            <div className="divTableCell">
              <Input
                name="top_ts"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={top_ts}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell"></div>
          </div>
          <div className="divTableRow3">
            <div className="divTableCell">TS</div>
            <div className="divTableCell">
              <Input
                name="ts"
                onChange={handleInput}
                step="0.1"
                max="100"
                value={ts}
              ></Input>
              <span className="inputunit"> % </span>
            </div>
            <div className="divTableCell">
              <Button onClick={mas} name="masts">
                +
              </Button>
              <Button onClick={menos} name="menosts">
                -
              </Button>
            </div>
          </div>
          <div className="divTableRow3">
            <div className="divTableCell">Tª</div>
            <div className="divTableCell">
              <Input
                onChange={handleInput}
                name="temp"
                step="1"
                max="100"
                value={values.temp}
              ></Input>
              <span className="inputunit"> ºC </span>
            </div>
            <div className="divTableCell">
              <Button onClick={mas} name="mastemp">
                +
              </Button>
              <Button onClick={menos} name="menostemp">
                -
              </Button>
            </div>
          </div>
          <div className="divTableRow3">
            <div className="divTableCell">Dens.</div>
            <div className="divTableCell">
              <Input
                name="dens"
                onChange={handleInput}
                step="1"
                max="1250"
                value={dens}
              ></Input>
              <span className="inputunit">
                kg/m<sup>3</sup>
              </span>
            </div>
            <div className="divTableCell">
              <Button onClick={mas} name="masdens">
                +
              </Button>
              <Button onClick={menos} name="menosdens">
                -
              </Button>
            </div>
          </div>
        </div>
      </div>
      <br></br>
      <div className="App">by JMD</div>
    </>
  );
}
