import React from "react";

const Inputradio = (props) => {
  return (
    <div onChange={props.onChange}>
      <label>
        <input type="radio" name="select" value="0"></input>  Memb.
      </label>
      <label>
        <input type="radio" name="select" value="1"></input>  Evap.
      </label>
    </div>
  );
};
export default Inputradio;
