import React from "react";

const Input = (props) => {
  return (
    <input
      onChange={props.onChange}
      name={props.name}
      value={props.value}
      type="number"
      className="input"
      step={props.step}
      min="0"
      max={props.max}
      placeholder=""
    />
  );
};
export default Input;
